.main__section {
}
.test .container {
  position: relative;
}
.test__main {
  display: flex;
  flex-direction: column;
}
.test__wrapper {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  width: 40%;
  border-radius: 2rem;
  background-color: #fff;
  padding: 2rem 4rem;
}
.test__title {
  font-size: 36px;
  margin-bottom: 2rem;
  text-align: center;
}
.test__description {
  font-size: 30px;
  text-align: center;
  margin-bottom: 3rem;
  line-height: 1.2;
}
.test__description .speaking-club {
  color: $mainRedColor;
}
.test__img {
  border-radius: 50%;
  border: 3px solid red;
  margin-bottom: 2rem;
}
.test__button {
}
.text__bg-img1 {
  position: absolute;
  top: 10%;
  left: 4%;
  width: 30rem;
  border-radius: 50%;
  opacity: 0.5;
  transform: rotate(-60deg);
}
.text__bg-img2 {
  position: absolute;
  top: 50%;
  left: 75%;
  width: 30rem;
  border-radius: 50%;
  opacity: 0.5;
}
.test__question {
  min-height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.test__question_counter {
  padding-top: 1rem;
  color: $mainRedColor;
  font-size: 3rem;
  text-align: right;
}
.test__question_text {
  font-size: 2.6rem;
}

.test__quiz {
  font-size: 2rem;
  line-height: 3rem;
  height: 3rem;
  display: flex;
  justify-content: space-between;
}
.test__quiz label {
  cursor: pointer;
}
.test__form_input {
  display: none;
}
.test__quiz input[type="radio"] + span,
.test__quiz input[type="checkbox"] + span {
  display:inline-block;
  position: relative;
  margin-left: 3.5rem;
}
.test__quiz input[type="radio"] + span:before {
  content:'';
  position: absolute;
  width: 2rem;
  height: 2rem;
  background-color: #F5F5F6;
  left: -3.5rem;
  top: 5px;
  border: 1px solid #C9CACB;
}
.test__quiz input[type="radio"] + span:before {
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  -khtml-border-radius: 50%;
}
.test__quiz input[type="radio"]:checked + span:after {
  content:'';
  position: absolute;
  width: 2rem;
  height: 2rem;
  background-color: $mainRedColor;
  left: -3.5rem;
  top: 5px;
  border: 1px solid #C9CACB;
}
.test__quiz input[type="radio"]:checked + span:after {
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  -khtml-border-radius: 50%;
}