.main__section {
}
.about {
  background-color: #FFF;
}
.about__wrapper {
}
.about__title {
  color: #000;
}
.about__title-item {
}
.about__inner {
}
.about__inner_text {
  font-size: 2.6rem;
  line-height: 1.4;
}
.about__inner_content {
/*   padding: 8rem 0; */
  position: relative;
  width: 100%;
}
.inner__content_item {
  position: absolute;
  color: #fff;
  border-radius: 1.4rem;
  background-color: rgba($color: $mainRedColor, $alpha: 1);
  z-index: 2;
  display: inline-block;
}
.inner__content_item:nth-child(1) {
  top: 15%;
  left: 65%;
}
.inner__content_item:nth-child(2) {
  top: 25%;
  left: 1%;
  width: 36%;
}
.inner__content_item:nth-child(3) {
  top: 60%;
  left: 2%;
}
.inner__content_item:nth-child(4) {
  top: 56%;
  left: 72%;
}
.inner__content_item:nth-child(5) {
  top: 96%;
  left: 50%;
  transform: translateX(-35%);
}
.content__item_descr {
  padding: 2rem;
  font-size: 1.8rem;
  position: relative;
}
.content__item_descr img {
  position: absolute;
  width: 4rem;
  top: -2rem;
  right: -2rem;
}
.inner__content_img {
  max-width: 55rem;
  display: block;
  margin: 0 auto;
  border-radius: 0 0 20% 20%;
  position: relative;
}
